@import './variables';

/* Container for the entire form */


.contact-wrapper {
    max-width: 600px;
    width:20rem;
    margin: 5rem auto;
    padding: 20px;
    background-color: #f9f9f9;
    border-radius: 10px;
    animation: boxShadowChange 25s infinite;
    
}

/* Styling for the title */
.page-title {
    display: block;
    font-size: 1rem;
    font-weight: bold;
    text-align: center;
    margin-bottom: 20px;
    color: #333;
}

/* Label styling */
form label {
    display: block;
    font-weight: bold;
    color: #555;
    text-align: center;
}

label {
    margin-bottom: 0.5rem;
}

/* Input field styling */
form input[type='text'], textarea {
    width: 100%;
    padding: 10px;
    margin-bottom: 20px;
    border: 1px solid #ccc;
    border-radius: 5px;
    font-size: 1rem;
    box-sizing: border-box;
    transition: border-color 0.3s ease-in-out;
    font-family: inherit;
}

/* Input focus state */
form input[type='text']:focus {
    border-color: #66afe9;
    outline: none;
}

.message {
   height: 10rem;
   padding-bottom: 13rem;
   scrollbar-width: 1px;
    resize: none;
    text-align: left; /* Aligns text to the left */
    vertical-align: top;
    text-wrap:balance;
}

/* Button styling */
form button[type='submit'] {
    display: block;
    width: 100%;
    padding: 12px;
    background-color: #007BFF;
    border: none;
    border-radius: 5px;
    color: white;
    font-size: 1.2rem;
    cursor: pointer;
    transition: background-color 0.3s ease-in-out;
}

/* Button hover state */
form button[type='submit']:hover {
    background-color: $acm-blue;
}

/* Responsive adjustments */
@media (max-width: 600px) {
    .contact-wrapper {
        padding: 15px;
        width: 15rem;
    }

    .page-title {
        font-size: 1.8rem;
    }

    form input[type='text'],
    form button[type='submit'] {
        font-size: 1rem;
    }

    form .message {
        height: 10rem;
    }
    
}



//modal

body.active-modal {
    overflow-y: hidden;
}

.modal, .overlay {
    width: 100vw;
    height: 100vh;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    position: fixed;
}

.overlay {
    background: rgba(49,49,49,0.8);
}
.modal-content {
    position: absolute;
    top: 40%;
    left: 50%;
    transform: translate(-50%, -50%);
    line-height: 1.4;
    background: #f1f1f1;
    padding: 14px 28px;
    border-radius: 3px;
    max-width: 600px;
    min-width: 300px;

    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}

.close-modal {
    position: absolute;
    top: 10px;
    right: 10px;
    padding: 5px 7px;
}

.close-btn {
    width: 6rem;
    height: 2rem;
}