@import './variables';

.posts-wrapper {
    padding: 2rem; 
    row-gap: 4rem;
    column-gap: 6rem;
    margin-top: 5vh;
    display: grid;
    grid-template-columns: repeat(3, auto);
    place-self: center;
    

    @media only screen and (max-width: 600px) {
        grid-template-columns: repeat(1, auto);
        margin-top: 1rem;
        row-gap: 3rem;
    }
}

.page-title{
    text-align: center;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 5vh;
}