$acm-blue: rgb(1,130, 172);
$acm-darkblue: rgb(27, 40, 86);
$acm-green: rgb(166, 188, 89);
$acm-lightblue: rgb(131,206,226);
$acm-purple: rgb(101,1,107);
$acm-dark: rgb(140, 0, 65);


@function darken-color($color, $percentage: 10%) {
    @return darken($color, $percentage);
  }


// Mixin for animation
@mixin color-cycle {
    animation: colorChange 10s infinite;
  }
  
  // Keyframes for color changes
  @keyframes colorChange {
    0%   { color: $acm-blue; }
    20%  { color: $acm-darkblue; }
    40%  { color: $acm-green; }
    60%  { color: $acm-purple; }
    80%  { color: $acm-dark; }
    100% { color: $acm-blue; }
  }

  @keyframes color{
    0%{
    background-position: 0 50%;
    }
    50%{
    background-position: 100% 50%;
    }
    100%{
    background-position: 0 50%;
    }
  }
  
  @keyframes boxShadowChange {
    0% {
        box-shadow: rgba(1, 130, 172, 0.8) 0px 30px 90px 6px; /* $acm-blue */
    }
    20% {
        box-shadow: rgba(27, 40, 86,  0.8) 0px 30px 90px 6px; /* $acm-darkblue */
    }
    40% {
        box-shadow: rgba(166, 188, 89,  0.8) 0px 30px 90px 6px; /* $acm-green */
    }
    60% {
        box-shadow: rgba(131, 206, 226,  0.8) 0px 30px 90px 6px; /* $acm-lightblue */
    }
    80% {
        box-shadow: rgba(101, 1, 107, 0.8) 0px 30px 90px 6px; /* $acm-purple */
    }
    100% {
        box-shadow: rgba(140, 0, 65,  0.8) 0px 30px 90px 6px; /* $acm-dark */
    }
}

