@import './variables.scss';

.footer {
    height: 50vh;
    margin-top: 5rem;
    background: linear-gradient(45deg, $acm-lightblue,$acm-blue, $acm-darkblue, $acm-dark, $acm-purple );
    background-size: 300% 300%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    animation: color 12s ease-in-out infinite;
    padding: 2rem 0 2rem 0;
    color: white;

    .logos {
        display: flex;
        justify-content: center;
        align-items: center;
        gap: 2rem; // Add spacing between the logos
        .logo {
            height: 5rem;
        }
    }

    a{
        color: white;
    }

    .icons {
        display: flex;
        justify-content: center;
        align-items: center;
        gap: 1rem;
      
        .icon {
            cursor: pointer;
            
            transition: color 300ms;
        }
        .icon:hover {
            color: black;
        }

    }

    @media only screen and (max-width: 600px) {
        padding: 2rem 2rem 2rem 2rem;
        height: 30vh;
        margin-top: 2rem;

        .logos {
          
            .logo {
                height: 3rem;
            }
          
        }
    }
}