@import './variables.scss';


.content-wrapper {
    margin-top: 2rem;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-bottom: 4rem;

    .board {
        margin-top: 2rem;
        display: grid;
        grid-template-columns: repeat(2, auto);
        gap: 4rem;
    }


    @media only screen and (max-width: 600px) {
        .board {
            grid-template-columns: repeat(1, auto);
        }
    }
}
