@import './variables.scss';

.member-wrapper{
  
    width: 15rem;
    height: 20rem;
    margin-top: 1rem;
    transition: all .2s ease-in-out; 
    border: 1px solid #ccc;
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    display: flex;
    flex-direction: column;
    align-items: center;

    text-align: center;


    img {
        height: 15rem;
        width: 15rem;
        border-top-right-radius: 8px;
        border-top-left-radius: 8px;
        border: none;
        
    }

    .text-wrapper {
        display: grid;
        grid-template-rows: 1.5fr 1fr 1fr;
        height: 5rem;

        
        
        
        .name {
            justify-content: center;
            align-items: center;
            display: flex;
            font-size: 1.25rem;
            font-weight: 600;
        }

        .role {
            justify-content: center;
            align-items: center;
            display: flex;
        }

        .icon-wrapper{
            display:flex;
            align-items: center;
            justify-content: center;
            gap: 1rem;

            .link {
                text-decoration: none;
                color: black;
                opacity: 0.8;
            }

            .link:hover {
                transform: scale(1.3);
                color: $acm-blue;
            }
        }

        
    }


}

.member-wrapper:hover {
    transform: scale(1.1);
    box-shadow: rgba(27, 40, 86, 0.5) 0px 54px 55px, rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px, rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;
    cursor: pointer;
}