@import './variables';

.post-card {

    width: 15rem; /* Adjust the width as needed */
    transition: all .2s ease-in-out; 
    border-radius: 8px;
    box-shadow: rgba(0, 0, 0, 0.16) 0px 3px 6px, rgba(0, 0, 0, 0.23) 0px 3px 6px;
    display: flex;
    flex-direction: column;
    align-items: center;

    .link {
        text-decoration: none;
        color: $acm-darkblue;
    }


    img {
        width: 15rem;
        height: 15rem;
        border-top-left-radius: 8px;
        border-top-right-radius: 8px;
    }

    .title {
        font-size: 1.2rem;
        text-align: center;
        margin-bottom: 0.5rem;
    }

    .date {
        font-size: 0.8rem;
    }
        
    .text-wrapper {
        flex: 1fr;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        padding: 1rem;


    }

    @media only screen and (max-width: 600px) {
        width: 12rem;

        img {
            width: 12rem;
            height: 12rem;
        }
    }


}




.post-card:hover {
    transform: scale(1.1);
    box-shadow: rgba(0, 0, 0, 0.56) 0px 22px 70px 4px;
}


//modal

body.active-modal {
    overflow-y: hidden;
}

.modal, .overlay {
    width: 100vw;
    height: 100vh;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    position: fixed;
}

.overlay {
    background: rgba(49,49,49,0.8);
}

.modal-content {
    position: absolute;
    top: 40%;
    left: 50%;
    transform: translate(-50%, -50%);
    line-height: 1.4;
    background: #f1f1f1;
    border-radius: 8px;
    width: 40vw;
    height: 50vh;

    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}




    .post-details {
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
        gap: 0.2rem;
        .post-title {
            text-align: center;
            font-size: 1.2rem;
        }

        .post-description {
            text-align: center;
            color: rgba(49,49,49,0.8) !important;
        }

        .post-link {
            text-decoration: none;
        }
}


.close-btn {
    width: 6rem;
    height: 2rem;
    margin-top: 2rem;
}