@import '../variables.scss';
.header {

    top: 0;
    left: 0;
    height: 14vh;
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: white;
    border-bottom: 1px solid rgba(129, 129, 129, 0.4);
    box-shadow: rgba(50, 50, 93, 0.25) 0px 2px 5px -1px, rgba(0, 0, 0, 0.3) 0px 1px 3px -1px;




    img {
        margin-left: 2rem;
        height: 60%;
        
    }


    .navbar {
        width: 20rem;
        padding-right: 2rem;
        display: flex;
        justify-content: space-between;
        align-items: center;
    }

    .link {
        text-decoration: none;
        color: $acm-darkblue;
    }

    .active-link {
        font-weight: 600;
        text-decoration: none;
    }

    @media only screen and (max-width: 600px) {
        
            display: flex;
            align-items: center;
            justify-content: space-between;
            padding: 0 1rem 0 0;

            .navbar {
                font-size: 0.8rem;
                padding-right: 1rem;
            }
        
    
        img {
            height: 4rem;
            margin-right: 1rem; 
            margin-left: 0.5rem;
        }

        .link {
            font-size: auto;
        }
    }
}



