
@import './variables';


.content-wrapper {

    margin-bottom: 8rem;

    .content-title {
        font-size: 2rem;
        margin-bottom: 4rem;
    }

    display: flex;
    align-items: center;

    .calendar {

        display: flex;
        align-items: center;
        flex-direction: column;


        .head {
            display: flex;
            flex-direction: row;
            justify-content: center;
            align-items: center;
            margin-bottom: 4rem;

            .heading {
                font-size: 1.5rem;
                font-weight: 600;
                padding: 0 1.5rem 0 1.5rem;
                width: 12rem;
                text-align: center;
            }

            .arrow{
                height: 1rem;
                padding: 0.5rem;
                border-radius: 50%;
                transition: all 200ms ease-in-out;
                cursor: pointer;
            }

            .arrow:hover{
                background-color: $acm-blue;
                color: white;
            }
    
        }
      
        .weekdays-grid {
            display: grid;
            width: 56rem;
            grid-template-columns: repeat(7, 8rem);
            margin-bottom: 2rem;

            .weekday-container {
                display: flex;
                justify-content: center;
                
            }
        }


        .days-grid{
            display: grid;
            grid-template-columns: repeat(7, 8rem);
            width: 56rem;
            height: 15rem;


            .day {
                display: flex;
                flex-direction: column;
                justify-content: center;
                align-items: center;
                border: solid 1px rgb(0, 0, 0, 0.2);
                height: 4rem;

                .event {
                    font-size: 0.6rem;
                    text-align: center;
                    margin-top: 0.5rem;
                }
            }

            .today {
                background-color: $acm-blue;
                color: white;
                border:solid 1px $acm-blue ; ;
            }


        }
    }
}